@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: medium;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: semibold;
  font-weight: 600;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: bold;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.list-null {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    max-width: 680px;
    padding-left: 0;
    padding-right: 0;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.header {
  background-color: #589be1;
  background-image: url("../img/background-header-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header {
    background-image: url("../img/background-header-tablet.jpg");
    box-shadow: none;
  }
}
@media (max-width: 470px) {
  .header {
    background-image: url("../img/background-header-mobile.jpg");
  }
}
@media (min-resolution: 2dppx) and (max-width: 470px) {
  .header {
    background-image: url("../img/background-header-mobile.jpg");
  }
}
@media (min-resolution: 2dppx) and (max-width: 1023px) {
  .header {
    background-image: url("../img/background-header-tablet@2x.jpg");
  }
}
@media (min-resolution: 2dppx) and (min-width: 1024px) {
  .header {
    background-image: url("../img/background-header-desktop@2x.jpg");
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 33px;
  padding-bottom: 35px;
}
@media (max-width: 1365px) {
  .header__container {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 1023px) {
  .header__container {
    align-items: normal;
    padding-top: 28px;
    padding-bottom: 28px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 18px;
    padding-bottom: 20px;
  }
}

.header__logo {
  margin-top: 2px;
}

.header__logo-icon {
  width: 70px;
  height: 38px;
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo-icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo-icon {
    width: 42px;
    height: 22px;
  }
}

.header__navigation {
  max-width: 70%;
}
@media (max-width: 1023px) {
  .header__navigation {
    max-width: 90%;
  }
}

.header__navigation-list {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  column-gap: 33px;
  row-gap: 10px;
}
@media (max-width: 1023px) {
  .header__navigation-list {
    column-gap: 24px;
  }
}

.header__navigation-link {
  font-family: "Montserrat", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__navigation-link {
    font-size: 14px;
    line-height: 17px;
  }
}

.header__navigation-toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__navigation-toggle {
    position: absolute;
    top: 10px;
    right: 0;
    display: block;
    width: 54px;
    height: 42px;
    border: none;
    background-color: transparent;
  }
}

.is-close .header__navigation-toggle::before {
  content: "";
  position: absolute;
  top: 13px;
  right: 50%;
  width: 24px;
  height: 2px;
  transform: translate(50%, 50%);
  background-color: #f9fbfd;
  box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
}

@media (max-width: 767px) {
  .is-close .header__navigation-list {
    display: none;
  }
}

@media (max-width: 767px) {
  .is-open .header__logo {
    visibility: hidden;
  }
  .is-open .header__navigation {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 89%;
    height: 100vh;
    background-color: #f9fbfd;
    background-image: url("../img/icons/logo/logo-menu.svg");
    background-repeat: no-repeat;
    background-position: 15px 20px;
  }
  .is-open .header__navigation-list {
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 10px;
    padding: 75px 30px 0 15px;
  }
  .is-open .header__navigation-link {
    display: block;
    padding: 10px;
    font-size: 18px;
    line-height: 21px;
    color: #011c40;
  }
  .is-open .header__navigation-toggle::before, .is-open .header__navigation-toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 11px;
    width: 18px;
    height: 2px;
    background-color: #011c40;
  }
  .is-open .header__navigation-toggle::before {
    transform: rotate(45deg);
  }
  .is-open .header__navigation-toggle::after {
    transform: rotate(-45deg);
  }
}

.promo {
  font-family: "Montserrat", "Arial", "Helvetica", sans-serif;
