.product {
  position: relative;
  height: 386px;
  cursor: pointer;
  transform-style: preserve-3d;
  perspective: 1000px;
}
.product:hover .product__wrapper, .product:focus-within .product__wrapper {
  transform: rotateY(180deg);
}
.product:hover .product__card, .product:focus-within .product__card {
  transform: rotateY(360deg);
}

.product__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  backface-visibility: hidden;
  transition-duration: 1s;
}

.product h3 {
  position: absolute;
  bottom: 22px;
  left: 30px;
  margin: 0;
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
