.second-title {
  margin: 0;

  font-family: "Montserrat", "Arial", "Helvetica", sans-serif;
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  color: $color-project-black;

  @media (max-width: 1023px) {
    font-size: 26px;
    line-height: 36px;
  }

  @media (max-width: 767px) {
    font-size: 24px;
    line-height: 33px;
  }
}
